@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@300;400;500&display=swap);
.AppRoot{
    display: -webkit-flex;
    display: flex;
    height: 100vh;
    overflow: hidden;
}
.Content{
    -webkit-flex: 1 0 auto;
            flex: 1 0 auto;
    overflow: auto;
}
/*MAPS*/
.zoom-top-bar{
    position: absolute;
    top:40%;
    right: 8px;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    z-index: 400;
}
.zoom-top-bar-btn{
    width: 40px;
    height: 40px;
    box-sizing: border-box;
    cursor: pointer;
    position: relative;
}
.zoom-top-bar-up{
    margin-bottom: 8px;
}
.btn-my-location, .my-location-2{
    width: 40px;
    height: 40px;
    cursor: pointer;
    right:8px;
    bottom: 30%;
    position: absolute;
    z-index: 400;
}
.zoom-top-bar-btn img, .to-image-btn img{
    width: inherit;
    height: inherit;
}
.left-menu-sidebar{
    position: relative;
    float: left;
    height: 100vh;
}
.pro-menu{
    position: absolute;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
}
.pro-menu .Footer{
    position: absolute;
    bottom: 0;
    width: 100%;
    text-align: center;
}
.CompanyNameSidebarMenu{
    cursor: pointer;
    margin: 0 auto;
    text-align: center;
}
.CompanyNameSidebarMenu .Icon{
    display: inline-block !important;
}
/*mocks_wrapper*/
.mocks_wrapper{
    clear: both;
    height: calc(100vh - 72px);
    overflow: hidden;
}
.mocks_wrapper > .left, .mocks_wrapper > .right{
    float: left;
    height: 100%;
    display: block;
    position: relative;
}
.mocks_wrapper > .left{
    width: 316px;
}
.mocks_wrapper > .right{
    border-left: 1px solid rgb(232 232 232);
    width: calc(100% - 318px);
}
.mocks_wrapper .scrolling-cell, .mocks_wrapper .scrolling-form{
    height: calc(100vh - 72px - 70px);
    overflow: scroll;
}
.mocks_wrapper .scrolling-cell, .mocks_wrapper .scrolling-form-no-header{
    height: calc(100vh - 72px);
    overflow: scroll;
}
.mocks_wrapper > .left .scrolling-cell .Cell > div{
    padding: 0 24px;
}
.mocks_wrapper > .left .Group, .mocks_wrapper > .right .Group{
    background-color: #fff;
    padding: 8px 0 20px 0;
}
.mocks_wrapper > .left .Group > .Group__inner, .mocks_wrapper > .right .Group > .Group__inner{
    padding: 0 !important;
}
.mocks_wrapper > .right{
    float: left;
}
.mocks_wrapper > .right .PanelHeader{
    position: relative;
    top: -5px;
}
.mocks_wrapper > .right .scrolling-form > form{
    padding: 0 16px;
}
.mocks_wrapper > .right .scrolling-form .FormStatus{
    margin: 12px !important;
}
.mocks_wrapper .scrolling-form{
    border-top: 1px solid rgb(232 232 232);
}
.mocks_wrapper .scrolling-form .FormLayoutGroup .FormItem:last-child h4{
    opacity: 0;
}
/*All settings maps containers*/
.PopoutWrapper{
    z-index: 9999999999999 !important;
}
/*Scrolling wrapper*/
.mocks_wrapper .left .SearchCustom{

}
.leaflet-custom-icon{
    border: none;
}
.leaflet-popup .leaflet-popup-tip-container{
    display: none !important;
}
.leaflet-popup .leaflet-popup-content-wrapper{
    /* White / White */

    background: #FFFFFF !important;
    /* Elevation 16 dp */

    box-shadow: 0 0 2px rgba(0, 0, 0, 0.12), 0 0 96px rgba(0, 0, 0, 0.16) !important;
    border-radius: 8px !important;
}
.leaflet-popup .leaflet-popup-close-button{
    display: none !important;
}
.leaflet-popup .leaflet-popup-content{
    width: auto !important;
    margin: 12px 24px 24px 24px;
}
.PopupViewMapData{
    min-width: 370px;
    font-family: 'IBM Plex Sans', sans-serif !important;
    font-weight: 400 !important;
    font-style: normal !important;
}

.PopupViewMapData .header .Cell h1{
    font-style: normal;
    font-weight: 500 !important;
    font-size: 18px;
    line-height: 26px;
    margin: 0 !important;
}
.PopupViewMapData .header .Cell .vkuiSimpleCell__main{
    padding: 0 !important;
}
.PopupViewMapData .header{
    height: 26px !important;
    box-sizing: border-box;
}
.PopupViewMapData .meta .vkuiSimpleCell__children{
    width: 100%;
}
.PopupViewMapData .meta .info-marker{
    padding-top: 8px;
    width: 338px;
}
.PopupViewMapData .meta .info-marker, .PopupViewMapData .meta .info-marker .info-marker-copy{
    line-height: 18px;
}
.PopupViewMapData .meta .info-marker .info-marker-copy{
    margin-left: 12px;
    position: relative;
    width: 18px;
    height: 18px;
    cursor: pointer;
}
.PopupViewMapData .meta .info-marker::-webkit-scrollbar{
    width: 0;
    height: 0;
}
.PopupViewMapData .meta .info-marker .info-marker-copy:hover{
    opacity: .9;
}
.PopupViewMapData .meta .info-marker .info-marker-copy img{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
.PopupViewMapData .meta .info-marker{
    font-size: 14px;
    color: #6F7985;
    font-weight: 300 !important;
}
.PopupViewMapData .settings{
    position: relative;
    width: inherit;
}
.PopupViewMapData .auto-light{
    clear: both;
    height: 22px;
    padding-top: 12px;
}
.PopupViewMapData .auto-light:first-child{
    padding-top: 0;
}
.PopupViewMapData .auto-light > div{
    float: left;
    height: 22px;
    line-height: 22px;
}
.PopupViewMapData .level_light_control{
    width: 338px;
    margin-top: 14px;
}
.PopupViewMapData .level_light_control.indicator{
    margin-top: 0;
}
.PopupViewMapData .level_light_control .FormItem{
    padding-left: 0;
    padding-right: 0;
}
.PopupViewMapData .level_light_control .FormItem h4{
    font: inherit !important;
    color: inherit;
    padding-bottom: 2px;
}
.PopupViewMapData .level_light_control .text__bottom{
    color: #6F7985;
    font-size: 14px;
    line-height: 18px;
    margin-top: 8px;
}
.PopupViewMapData .indicator .Slider__thumb{
    display: none;
}
.PopupViewMapData .indicator .vkuiSlider--disabled{
    opacity: 1 !important;
}
.PopupViewMapData .temperature{
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: #6F7985;
    margin-left: 12px;
}

